.updateInput {
    display: flex;
    flex-direction: column;
}

.updateInput .updateInputData input {
    flex-direction: row;
    margin-left: 10px;
    margin-bottom: 10px;
    padding: 5px;
    border-radius: 4px;
    border: 1px solid gray;
}

.updateInput .updateButton button {
    margin-left: 10px;
    margin-bottom: 10px;
    border: none;
    width: 90px;
    padding: 7px;
    border-radius: 7px;
    background-color: rgb(154, 154, 242);

}


@media screen and (max-width: 960px) {
    .updateInputData input {
        display: flex;
        flex-direction: column;
        padding: 10px;
    }

}

@media screen and (max-width: 768px) {
    .updateInputData input {
        display: flex;
        flex-direction: column;
        padding: 10px;
    }

}