.teacherUpdateImg {
    display: flex;
    justify-content: center;
    background-color: rgb(47, 47, 47);
    height: 230px;
    width: 230px;
    border-radius: 50%;
    align-items: center;
    margin-bottom: 20px;
}

.teacherUpdateImg img {
    height: 200px;
    width: 200px;
    border-radius: 50%;
}

.teacherUpdateImg img:hover{
    opacity: 0.4;
}