.mainSearch {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.mainSearch input {
    width: 300px;
    padding: 10px;
    border: none;
    border-radius: 4px;
}

.mainSearch button {
    width: 100px;
    margin-left: 10px;
    padding: 10px;
    border: none;
    border-radius: 4px;
}

.mainSearch button:hover {
    background-color: black;
    color: white;
    transition: all 0.3s ease;
}

@media screen and (max-width: 960px) {
    .mainSearch form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .mainSearch form input {
        margin-bottom: 10px;
    }

  }
  
@media screen and (max-width: 768px) {
    .mainSearch form {
        display: flex;
        flex-direction: column;
        width: 100%;
        justify-content: center;
        align-items: center;

    }

}