.studentData {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 90px;
    padding: 20px;
}


.studentData .centerDataHeader {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30%;
    padding-top: 10px;
    padding-bottom: 10px;
    background-color: gray;
    margin-bottom: 20px;
    border: none;
    border-radius: 4px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.studentData .centerDataBody {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: 70vh;
    background-color: gray;
    border: none;
    border-radius: 4px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media screen and (max-width: 960px) {
    .studentData {
        margin-top: 1px;
    }
    .studentData .centerDataHeader {
        width: 40%;
    }

  }
  
  @media screen and (max-width: 768px) {
    
    .studentData .centerDataHeader {
        width: 80%;
    }
 
  }