.gradeContainer {
    display: flex;
    justify-content: row;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
    width: 100%;

}

.gradeContainer .gradeNested {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 80%;
    justify-content: center;
}

.gradeContainer .gradeNested .gradeLink {
    padding: 20px;
}

.gradeLink button {
    padding: 20px;
    width: 200px;
    height: 100px;
    border: none;
    border-radius: 5px;
}

.gradeLink button:hover {
    background-color: gray;
    color: white;

}

@media screen and (max-width: 960px) {
    .gradeContainer .gradeNested {
        display: flex;
        flex-direction: row;
    }

    .gradeLink button {
        width: 180px;
    }

  }
  
  @media screen and (max-width: 768px) {
    .gradeContainer .gradeNested {
        display: flex;
        flex-direction: column;
    }

  }