.cardContain {
    display: flex;
    flex-direction: row;
}

.myCard {
    width: 100%;
    background-color: gray;
    border: none;
    border-radius: 5px;
    height: 550px;
    padding: 3px;
}

.myCard .cardHeader {
    background-color: rgb(70, 70, 70);
    padding: 10px;
    color: white;
    border-radius: 5px;
    margin-left: 3px;
    margin-right: 3px;
    margin-top: 3px;
}

@media screen and (max-width: 960px) {
    .cardContain {
        display: flex;
        flex-direction: column;
    }

    .myCard {
        margin-top: 20px;
    }



  }
  
  @media screen and (max-width: 768px) {
    .cardContain {
        display: flex;
        flex-direction: column;
    }

    .myCard {
        margin-top: 20px;
    }


 
  }