.card {
  width: 300px;
  text-align: center;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.5);
  background-color: #d4dbe2;
  padding: 1rem;
  margin-left: 1rem;
  margin-bottom: 1rem;
}

.card__title {
  font-size: 1rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

.card__author {
  font-size: 1rem;
  margin-bottom: 1rem;
}

.card__body {
  width: 100%;
  height: 300px;
  border-radius: 5px;
  object-fit: fill;
  cursor: pointer;
}


